.ant-layout {
  display: flex;
  min-height: 0;
  flex-direction: column;
  box-sizing: border-box;
}
.ant-layout .ant-layout-header {
  height: 40px;
  padding: 10px;
  line-height: 25px;
  background: #001529;
}
.ant-layout .ant-layout-header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}
.ant-layout .ant-layout-header .logo .cat {
  height: 18px;
  width: 1px;
  display: inline-block;
  background-color: #4e4e4e;
  margin: 0 8px;
}
.ant-layout .ant-layout-header .logo .header-tagname {
  font-size: 16px;
  color: #fff;
}
